let callbacks = [];
const invoke = fn => fn();
const tick = () => {
  const thisTicksCallbacks = [...callbacks];
  callbacks = [];
  thisTicksCallbacks.forEach(invoke);
  requestAnimationFrame(tick);
};
requestAnimationFrame(tick);

export default function raf(cb) {
  callbacks.push(cb);
}



